.table-cont {
  width: 700px;
  height: 449px;
  display: flex;
  flex-direction: column;
  font-family: "Roboto Slab";
}
.table-row {
  width: 100%;
  height: 48px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.table-row-left {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: auto;
}
.table-row-rigth {
  height: 100%;
  width: 391px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.first-icon {
  margin-left: 6px;
  margin-right: 10px;
}
.last-icon {
  margin-right: 67px;
  margin-left: 44px;
  cursor: pointer;
}
.table-icon {
  width: 18px;
}
.table-span {
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 18px;

  color: #252c41;
}
.table-items {
}
.row-title {
  font-weight: normal;
}
